import { TWENTY_FOUR_MOUNTAIN } from '../../cm-lib/constants/index'
import { dateObjectToBazi } from '../../cm-lib/FourPillar/index'
import { createFlyingstarChart, getDirectionByIndex, getAnnualStar, createBasePlate } from '../../cm-lib/XuanKongFlyingStar'
import { getBazhaiChart } from '../../cm-lib/EightMansion/'

const mountains = TWENTY_FOUR_MOUNTAIN.map(m => m.name)

export const generateGrandDuke = () => {
    const bazi = dateObjectToBazi(new Date(), 'm');
    const yearBranch = bazi.GanZhiYear.ganzhi.slice(1);
    const data = mountains.map((mountain, index) => {

        const match = mountain === yearBranch
        return {
            mountain,
            backgroundColor: match ? 'brown' : 'transparent',
            label: match ? '太岁' : ''
        }
    })
    return {
        grandDuke: {
            color: 'white',
            font: {
                size: 16,
            },
            backgroundColor: data.map(e => e.backgroundColor),
            borderRadius: 4,
            offset: 210,
            padding: 4,
            formatter: (val, ctx) => data.map(e => e.label)[ctx.dataIndex],
            align: 'end',
            anchor: 'start',
            clamp: true,
        },
    }
}


export const generateFiveYellow = () => {
    const now = new Date()
    const annualStar = getAnnualStar(now)
    const periodPlate = createBasePlate(annualStar)
    const index = periodPlate.findIndex(e => e === 5)
    const direction = getDirectionByIndex(index)

    const data = TWENTY_FOUR_MOUNTAIN.map(mountain => {
        const is5Y = mountain.facing.slice(0, -1) === direction.toLowerCase()
        return {
            backgroundColor: is5Y ? 'yellow' : 'transparent',
            label: is5Y ? '五黄' : ''
        }
    })
    if (direction) {
        return {
            fiveYellow: {
                color: '#8B8000',
                font: {
                    size: 16,
                },
                backgroundColor: data.map(e => e.backgroundColor),
                borderRadius: 4,
                offset: 160,
                padding: 4,
                formatter: (val, ctx) => data.map(e => e.label)[ctx.dataIndex],
                align: 'end',
                anchor: 'start',
                clamp: true,
            },
        }
    }
    return {}
}

export const generateFlyingStar = (period, facing) => {

    const chart = createFlyingstarChart(facing, period)
    const data = TWENTY_FOUR_MOUNTAIN.map(mountain => {

        const match = chart.find(grid => grid.direction.toLowerCase() + '2' === mountain.facing)
        const star = match?.waterNumber + '-' + match?.baseNumber + '-' + match?.mountainNumber
        return {
            backgroundColor: !!match ? 'blue' : 'transparent',
            label: !!match ? star : ''
        }

    })
    return {
        name: {
            color: 'white',
            font: {
                size: 16,
            },
            backgroundColor: data.map(e => e.backgroundColor),
            borderRadius: 4,
            offset: 10,
            padding: 10,
            formatter: (val, ctx) => data.map(e => e.label)[ctx.dataIndex],
            align: 'end',
            anchor: 'center',
            clamp: true,
        },
    }
}

export const generateMountain = (params) => {
    const { facing } = params.facing

    const mountain = TWENTY_FOUR_MOUNTAIN.find(e => e.facing === facing)
    const data = TWENTY_FOUR_MOUNTAIN.map((m, i) => {
        let color = 'yellow'
        if (m.facing === mountain.facing) {

            color = 'red'
        }

        if (m.facing === mountain.sitting) {

            color = 'black'
        }
        return {
            color
        }
    })

    return {
        mountains: {
            color: data.map(e => e.color),
            font: {
                size: 24,
            },
            borderRadius: 4,
            offset: 2,
            padding: 10,
            formatter: (val, ctx) => mountains[ctx.dataIndex] || '',
            align: 'start',
            anchor: 'end',
            clamp: true,
        },
    }
}

export const getBaZhai = (params) => {
    const { facing, isMobile } = params
    const color = {
        green: 'rgba(46, 204, 113, 0.3)',
        red: 'rgb( 255, 99, 132, 0.3)',
        gray: 'rgb(192,192,192, 0.3)'
    }
    const chart = facing ? getBazhaiChart(facing?.facing) : null
    const data = TWENTY_FOUR_MOUNTAIN.map((mountain, i) => {

        const getStar = (direction, chart) => {
            return chart ? Object.keys(chart).find(k => chart[k].toLowerCase() === direction.slice(0, -1)) : ''
        }

        const star = getStar(mountain.facing, chart);
        let arcColor = ''
        if (["伏位", "延年", "生气", "天医"].includes(star)) {
            arcColor = color.green
        } else {
            arcColor = color.red
        }
        return {
            arcColor,
            label: star
        }
    })

    return {
        bazhai: {
            color: 'white',
            font: {
                size: 16,
            },
            backgroundColor: isMobile ? "transparent" : 'black',
            borderRadius: 4,
            offset: 110,
            padding: 10,
            formatter: (val, ctx) => isMobile ? "" : data.map(e => e.label)[ctx.dataIndex],
            align: 'start',
            anchor: 'end',
            clamp: true,
        },
        arcColor: data.map(e => e.arcColor),
    }
}

export const generateArrow = (facing) => {
    const mountain = TWENTY_FOUR_MOUNTAIN.find(e => e.facing === facing)
    const data = TWENTY_FOUR_MOUNTAIN.map((m, i) => {
        let renderValue = ''
        let color = 'red'
        if (m.facing === mountain.facing) {
            renderValue = '向'
            color = 'red'
        }

        if (m.facing === mountain.sitting) {
            renderValue = '坐'
            color = 'black'
        }

        return {
            value: renderValue,
            color,
            backgroundColor: renderValue ? 'white' : 'transparent'
        }
    })



    return {
        arrow: {
            color: 'red',
            font: {
                size: 24,
            },
            backgroundColor: data.map(e => e.backgroundColor),
            color: data.map(e => e.color),
            borderRadius: 4,
            offset: 42,
            padding: 2,
            formatter: (val, ctx) => data.map(e => e.value)[ctx.dataIndex],
            align: 'start',
            anchor: 'end',
            clamp: false,
        },
    }
}

const generateYiJingMethod = (params) => {
    const { isMobile } = params
    const rooms = ['e2', 'se2', 'sw2', 'nw2'];
    const secondaryRooms = ['s2', 'w2', 'ne2'];
    const color = {
        green: 'rgba(46, 204, 113, 0.3)',
        red: 'rgb( 255, 99, 132, 0.3)',
        gray: 'rgb(220,220,220, 0.3)'
    }
    const data = TWENTY_FOUR_MOUNTAIN.map(e => {
        let arcColor = color.gray;
        let label = ''
        let backgroundColor = 'transparent'
        if (rooms.includes(e.facing)) {
            arcColor = color.red
            backgroundColor = 'black'
        }
        if (secondaryRooms.includes(e.facing)) {
            arcColor = color.green
            backgroundColor = 'black'
        }

        switch (e.facing) {
            case 'e2':
                label = '儿子'
                break;
            case 'se2':
                label = '女儿'
                break;
            case 'sw2':
                label = '客房'
                break;
            case 'nw2':
                label = '夫妻'
                break;
            case 's2':
                label = '备选房'
                break;
            case 'w2':
                label = '备选房'
                break;
            case 'ne2':
                label = '爷奶'
                break;
        }
        return {
            arcColor,
            label,
            backgroundColor
        }

    })
    return {
        config: {
            color: 'white',
            font: {
                size: 16,
            },
            backgroundColor: data.map(e => isMobile ? 'transparent' : e.backgroundColor),
            borderRadius: 4,
            offset: 110,
            padding: 10,
            formatter: (val, ctx) => isMobile ? '' : data.map(e => e.label)[ctx.dataIndex],
            align: 'start',
            anchor: 'end',
            clamp: true,
        },
        arcColor: data.map(e => e.arcColor),
    }
}


export const getConfig = (method, params) => {
    let config = '';

    switch (method) {
        case 'EM':
            config = getBaZhai(params)
            return {
                config: {
                    config: config.bazhai,
                    ...generateFiveYellow(),
                    ...generateGrandDuke(),
                    ...generateMountain(params),
                },
                arcColor: config.arcColor,
            }
        case 'EMM':
            config = getBaZhai(params)
            return {
                config: {
                    config: config.bazhai,
                    ...generateMountain(params),
                },
                arcColor: config.arcColor,
            }
        case 'YJM':
            config = generateYiJingMethod(params);
            return {
                config: {
                    config: config.config,
                    ...generateMountain(params),

                },
                arcColor: config.arcColor,

            }
        case 'YJ':
            config = generateYiJingMethod(params);
            return {
                config: {
                    config: config.config,
                    ...generateMountain(params),

                },
                arcColor: config.arcColor,

            }
    }
}