import React, { useEffect, useState, useRef } from 'react';
import Navbar from './global-components/navbar-v2';
import ErrorPage from './section-components/error';
import Footer from './global-components/footer';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie, getElementAtEvent } from 'react-chartjs-2';
import Draggable, { DraggableCore } from "react-draggable";
import ChartDataLabels from "chartjs-plugin-datalabels";
import { getConfig, getBaZhai, generateFiveYellow, generateArrow, generateGrandDuke, generateFlyingStar, generateMountain } from "./helpers/CompassConfig"
import { TWENTY_FOUR_MOUNTAIN } from '../cm-lib/constants/index'
import useWindowDimensions from '../hook/useWindowDimension'
ChartJS.register(ChartDataLabels);
ChartJS.register(ArcElement, Tooltip, Legend);

const color = {
  green: 'rgba(46, 204, 113, 0.3)',
  red: 'rgb( 255, 99, 132, 0.3)',
  gray: 'rgb(192,192,192, 0.3)'
}

const Component = ({
  children,
  degree,
  handleClick,
  labels,
  backgroundColor,
  centerText = [],
  edgeText = [],
  fiveYellow,
  facing,
  period,
  convention
}) => {


  const { width } = useWindowDimensions();
  const isMobile = width < 859
  const { method } = convention;
  const chartRef = useRef();
  const rotation = (degree - 157.5) * -1;

  const data = [...Array(24).keys()].map(() => 100 / 24);
  const config = getConfig(method, {
    facing: facing,
    isMobile
  });

  const arcColor = config?.arcColor || [
    color.red,
    color.green
  ]
  console.log(config.config)
  const datalabels = {
    labels: {
      ...config.config
    }
  }

  return <div className='overlayContainer'>
    <Draggable>
      <div className='overlay'>
        <Pie
          onClick={(event) => {
            const dom = chartRef.current;
            const _selected = getElementAtEvent(dom, event);
            handleClick(event, dom, TWENTY_FOUR_MOUNTAIN[_selected[0]?.index])
          }}
          onContextMenu={(event) => {
            const dom = chartRef.current;
            const _selected = getElementAtEvent(dom, event);
            handleClick(event, dom, TWENTY_FOUR_MOUNTAIN[_selected[0]?.index])
          }}
          ref={chartRef}
          options={{

            events: ['mousemove', 'mouseout', 'click', 'touchstart', 'touchmove'],
            rotation,
            plugins: {
              legend: {
                display: false
              },
              datalabels: {
                display: true,
                color: '#36A2EB'
              },
              tooltip: {
                enabled: false
              }
            }
          }}
          data={{
            labels,
            datasets: [{

              data,
              backgroundColor: arcColor,
              hoverOffset: 4,
              datalabels,
            }]
          }}

        />
      </div>

    </Draggable>

    {children}
  </div>

}

export default Component
