import React, { useState } from 'react';
import Navbar from './global-components/navbar-v2';
import ErrorPage from './section-components/error';
import Footer from './global-components/footer';

const Home_V1 = ({ children }) => {
  const [minimise, setMinimise] = useState(false)

  return <div className="toolbar">
    {
      minimise && <span style={{ cursor: 'pointer', 'float': 'right' }} onClick={() => setMinimise(false)} >Toolbar</span>
    }
    {
      !minimise && <><span className="icon-minus" style={{ cursor: 'pointer', 'float': 'right' }}
        onClick={() => setMinimise(true)}

      >hide</span>
        {children}
      </>
    }

  </div>
}

export default Home_V1
