import * as React from "react"
import { useState } from "react"
import { HeadFC, PageProps, graphql, useStaticQuery } from "gatsby"
import Ad from '../components/ad'
import { Trans, useTranslation } from 'gatsby-plugin-react-i18next';
import Layout from '../components/layout/layout'
import '../assets/css/main.scss'
import Pie from '../components/Pie'
import Toolbar from '../components/Toolbar'
import Area from '../components/Area'
import { TWENTY_FOUR_MOUNTAIN } from '../cm-lib/constants/index'
import useWindowDimensions from '../hook/useWindowDimension'

const IndexPage: React.FC = () => {

  const { width } = useWindowDimensions();
  const isMobile = width < 859
  const { t } = useTranslation();
  const [rotation, setRotation] = useState(0);
  const [floorplan, setFloorplan] = useState(null);
  const [dropDownCoordinate, setDropDownCoordinate] = useState({
    x: '-999999px',
    y: '-999999px',
  })

  const [selectedArea, setSelectedArea] = useState(null);
  const [configuration, setConfiguration] = useState({
    facing: TWENTY_FOUR_MOUNTAIN[13],
    fiveYellow: '',
    convention: {
      method: isMobile ? 'EMM'  : 'EM'
    }
  })

  const setConvention = (e) => {
    {
      setConfiguration({
        ...configuration,
        convention: {
          method: isMobile ? e.target.value + 'M'  : e.target.value
        }
      })
    }
  }
  const [displayOption, setDisplayOption] = useState(false)

  const handleRotation = (add) => {
    if (rotation + add === -1) {
      setRotation(359)
    }
    if (rotation + add === 360) {
      setRotation(0)
    }
    const _answer = rotation + add;
    setRotation(_answer)
  }

  const handleClick = (event, dom, data) => {

    if (data && event.type === 'contextmenu') {
      let vw = window.innerWidth,
        vh = window.innerHeight,
        eCXinVW = event.clientX / vw * 100, // Multiplying by 100 to get percentage 
        eCYinVH = event.clientX / vh * 100; // Multiplying by 100 to get percentage
      const coord = {
        x: `${event.pageX}px`,
        y: `${event.pageY}px`,
      }
      console.log(coord)
      setDropDownCoordinate(coord)

      setSelectedArea(data)
      setDisplayOption(true)
      event.stopPropagation();
      event.preventDefault();
    }

  }

  return (
    <Layout onFloorplan={setFloorplan}>
      {
        displayOption && <Area
          x={dropDownCoordinate.x}
          y={dropDownCoordinate.y}
        >
          <div>
            <button onClick={() => {
              setConfiguration({
                ...configuration,
                facing: selectedArea
              })
            }}>设置向位</button>


          </div>

        </Area>
      }
      {
        floorplan && <Toolbar>
          <select onChange={setConvention}>
            <option value="EM">八宅 Eight Mansion</option>
            <option value="YJ">倪海厦 阳宅 </option>
          </select>
          <br />
          <button onMouseDown={() => handleRotation(-1)}>{"<<"}</button>
          <button onMouseDown={() => handleRotation(1)}>{">>"}</button>
          <input
            id="typeinp"
            type="range"
            min="0" max="359"
            value={rotation}
            onChange={(e) => setRotation(Number(e.target.value))}
            step="1" />
          <div>
            {
              rotation
            }

          </div>
          向: {configuration?.facing?.name || 'Not Set'}
        </Toolbar>
      }
      <main className="page-main" onClick={() => setDisplayOption(false)}>
        <div className="section-banner relative">


          <div className="section-banner__bg" style={{ backgroundImage: "url(assets/img/bg-banner.jpg)" }} >
            <div className="uk-container uk-text-center">
              <div className="section-banner__content">
                <div className="section-banner__form">
                  {
                    configuration?.convention?.method === 'YJ' && <h3 style={{ color: 'white' }}>To know more about 倪海厦 阳宅. Can click <a href="http://www.finalhopes.com/yangzhai/index.html">here</a>.
                    OpenFengShui are not related to the website in any way.
                    </h3>
                  }
                  {
                    !floorplan && <h3 style={{ color: 'white' }}>Start by uploading your floorplan. Some Feature may not be available on mobile.</h3>
                  }
                  {
                    floorplan && <Pie

                      degree={rotation}
                      handleClick={handleClick}
                      facing={configuration.facing}
                      fiveYellow={configuration.fiveYellow}
                      period={7}
                      convention={configuration.convention}
                    >
                      <img src={'data:image/jpeg;base64,' + btoa(floorplan)} alt="image" />
                    </Pie>
                  }
                </div>
                <div className="section-banner__bottom">

                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="section-steps">
          <div className="uk-section-large uk-container">
            <div className="section-title uk-text-center">
              <Ad />
            </div>
          </div>
        </div>
      </main>
    </Layout>
  )
}

export default IndexPage

export const Head: HeadFC = () => <title>Free Feng Shui Tool</title>


export const query = graphql`
  query($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
